#start {
  z-index: 999;
  position: absolute;
  margin: 0 auto;

}

html {
  /* border-box box model allows us to add padding and border to our elements without increasing their size */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /* A system font stack so things load nice and quick! */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  /* font-weight: 900;
  font-size: 10px;
  color: var(--black); */
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
  width: 100%;
height: 100%;
  
}

/*
  WAT IS THIS?!
  We inherit box-sizing: border-box; from our <html> selector
  Apparently this is a bit better than applying box-sizing: border-box; directly to the * selector
*/
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
margin: 0; 
margin: 0;
padding: 0;
font-family: sans-serif;
background: #355C7D;  /* fallback for old browsers */
/* background: -webkit-linear-gradient(to bottom, #355C7D,  #e8cbc0);  Chrome 10-25, Safari 5.1-6 */
/* background: linear-gradient(to bottom, #355C7D,  #e8cbc0); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

background-image: -webkit-gradient(linear, left top, left bottom, from(#355C7D), to(#e8cbc0));

background-image: -webkit-linear-gradient(top, #355C7D 0%, #e8cbc0 100%);

background-image: -o-linear-gradient(top, #355C7D 0%, #e8cbc0 100%);

background-image: linear-gradient(to bottom, #355C7D 0%, #e8cbc0 100%);

/* background-attachment: fixed; */
overflow: hidden;

/* background-image: url("pics/cloudshor.png"); */
}

.One {
  overflow: hidden;
  display: inline;
  height: 100vh;
  /* background-image: linear-gradient(to bottom, #e7e7ef 0%, #75ACDD 100%);  */
  left: 400%;
  -webkit-animation-name: bounce;
  -webkit-animation-duration: 100s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
  -webkit-animation-timing-function: linear;
}

#box:hover {
  -webkit-animation-play-state: paused;
}

@-webkit-keyframes bounce {
  0% {
      margin-left: 0;
  }
  100% {
      margin-left: -400px;
  }
}

ul {
  padding: 0
}



@import url('https://fonts.googleapis.com/css?family=Raleway:600|Source+Sans+Pro');

